*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
$bg-color: #f9f8;

.registration{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $bg-color;


}

.registration__form{
  width: 50%;
  height: 95%;
  position: absolute;
  top: 4%;
  right: 40%;
  background: #fff;
  
  box-shadow: 10px 10px 50px gray;

}

@media screen and (max-width:920px) {
  
  .registration__form{
    width: 70%;
    height: 70%;
    position: absolute;
    top: 4%;
    right: 20%;
  
  }
}

@media screen and (max-width:375px) {
  
  .registration__form{
    width: 80%;
    height: 90%;
    position: absolute;
    top: 4%;
    right: 10%;
  
  }
}
.form__header{
  display: flex;
  margin-top: .4%;
  margin-left: 4%;
  margin-bottom: .1%;
}


.form__group{
 
 width: 90%;
 height: 100%;
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 justify-content: flex-start;
 margin-left: 4%;

}
.input__wrapper{
  width: 90%;
  height: 5%;
}
.text__label{
  font-size: .9rem;
  font-weight: 300;
  margin-left: 0%;
  margin-top: 0.5%;
  display: flex;
  

}
.form__control{
  width: 60%;
  height: 30%;
  padding: 2%;
  border-radius: 2%;
  border: 0;
  display: flex;
  border: .05px solid #000;

}
@media screen and (max-width:375px) {
  
  .form__control{
    width: 100%;
  }
}
@media screen and (max-width:820px) {
  
  .form__control{
    width: 100%;
  }
}
.help__block{
  width: 90%;
  height: 10%;
  color: red;
  font-size: 1rem;
  font-weight: 300;
  display: flex;

}

.help_parent{
  width: 90%;
  height: 5%;
  padding: 1%;

}
.form__submit{
  display: flex;
  margin-top: 2%;
  margin-left: 2%;
  &--btn{
    background: #66D3FE;
    outline: none;
    border: none;
    padding: 1.5%;
    border-radius: 4%;
    box-shadow: 0 10px 50px gray;
    transition: all .2s linear;
    cursor:pointer;
    &:hover{
      transform: scale(1.05);
    }
  }
}

