*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

.dashboard__container{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #D3D3D3;
}


.welcome__greeting{
  width: 50%;
  height: 4%;
  position: absolute;
  top: 5%;
  left: 20%;
  display: flex;
  align-items: center;
  justify-content: space-around; 
}
.logout{
  font-size: 1rem;
  width: 20%;
  height: 50%;
  background: #e8e8;
  outline: none;
  border: none;
  padding: 4%;
  border-radius: 4%;
  box-shadow: 0 10px 50px gray;
  cursor:pointer;
  &:hover{
    transform: scale(1.05);
  }
}

@media screen and (max-width:375px) {
  
  .logout{
    font-size: 1rem;
    width: 50%;
    height: 50%;
  
    padding: 17%;
   
  }
}